import React, { useState } from 'react'
import { CCard, CCardBody, CCardHeader, CRow, CButton, CInputGroup, CInputGroupText, CFormInput } from '@coreui/react'
import networkCall from '../utils/networkCall'


const AddProduct = () => {
  const [name, setName] = useState('')
  const [quantity, setQauntity] = useState(0)
  const [price, setPrice] = useState(0)


  const saveProduct = () => {
    if (name != '' && quantity != 0 && price != 0) {
        networkCall({name: name, quantity: quantity, price: price}, (data) => {
            console.log({name: name, quantity: quantity, price: price})
        },'POST', 'products')
    }
  }
  return (
    <CRow>
      <CCard className="mb-4">
        <CCardHeader>Add Product</CCardHeader>
        <CCardBody>
          <CInputGroup className="mb-3">
            <CInputGroupText>Name</CInputGroupText>
            <CFormInput aria-label="quantity" type='text' onChange={(evnt) => setName(evnt.target.value)} />
            {/* <CInputGroupText>.00</CInputGroupText> */}
          </CInputGroup>
          <CInputGroup className="mb-3">
            <CInputGroupText>Quantity</CInputGroupText>
            <CFormInput aria-label="quantity" type='number' onChange={(evnt) => setQauntity(evnt.target.value)} />
            {/* <CInputGroupText>.00</CInputGroupText> */}
          </CInputGroup>
          <CInputGroup className="mb-3">
            <CInputGroupText>Price</CInputGroupText>
            <CFormInput aria-label="quantity" type='number' onChange={(evnt) => setPrice(evnt.target.value)} />
            {/* <CInputGroupText>.00</CInputGroupText> */}
          </CInputGroup>
          <CButton component="a" color="success" href="#" role="button" onClick={(evnt) => saveProduct()}>
            Save
          </CButton>
        </CCardBody>
      </CCard>
    </CRow>
  )
}

export default AddProduct
