import logo from './logo.svg';
import './App.css';
import './scss/style.scss'
import Dashboard from './layout/Dashboard'
import { Container } from './layout/Container';

import React, { Component, Suspense } from 'react'
// import { HashRouter, Route, Router, Routes } from 'react-router-dom'
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Order from './layout/Order';
import Checkout from './layout/Checkout';
import OrderList from './layout/OrderList';
import AddProduct from './layout/AddProduct';
import Login from './layout/Login';

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

const Content = () => {
  return (
    <>
      <Container />
      <div className="container wrapper d-flex flex-column min-vh-100" style={{ paddingTop: '20px' }}>
        <Suspense fallback={loading}>
          <Routes>
            <Route exact path="/dashboard" element={<Dashboard />} />
            <Route exact path="/order" element={<Order />} />
            <Route exact path="/checkout" element={<Checkout />} />
            <Route exact path="/order_list" element={<OrderList />} />
            <Route exact path="/add_product" element={<AddProduct />} />
          </Routes>
        </Suspense>
      </div>

    </>
  )
}
function App() {
  return (

    <div >
      <Router>
        <Routes>
          <Route exact path="/" element={<Login />} />
          <Route exact path="/*" element={<Content />} />
        </Routes>
      </Router>

    </div>


  );
}

export default App;
