import React, { useEffect, useState } from 'react'
import { CCard, CCardBody, CCardHeader, CCol, CFormSelect, CRow, CButton, CInputGroup, CInputGroupText, CFormInput } from '@coreui/react'

import {
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from '@coreui/react'
import networkCall from '../utils/networkCall'
import { useNavigate } from 'react-router-dom'

const Order = () => {

  const navigate = useNavigate()
  const tableExample = []

  const [data, setData] = useState([...tableExample])
  const [products, setProducts] = useState([])

  const [product, setProduct] = useState('')
  const [quantity, setQauntity] = useState(0)
  const [price, setPrice] = useState(0)


  useEffect(() => {
    networkCall({}, (data) => {
      setProducts([...data])
    }, 'GET', 'products')
  }, [])

  useEffect(() => {
    if(products.length > 0){
      setProduct(products[0].name)
      setPrice(products[0].price)
    }
  }, [products])

  const addOrder = () => {
    let tmp = {
      name: product,
      quantity: quantity,
      price: price
    }

    console.log(price)
    if (product != '' && quantity != 0) {
      console.log(tmp)
      setData([...data, tmp])
    }



  }


  const saveOrder = () => {
    let totalPrice = 0
    data.forEach((value, index) => {
      totalPrice += value.price * value.quantity
    })
    let tmp = {
      price: totalPrice,
      vat: totalPrice * 0.15
    }
    networkCall({ price: totalPrice }, (data) => {
      alert("Saved Order")
    }, 'POST', 'orders')

    navigate('/checkout', { state: tmp });
    // console.log(tmp)

  }
  return (
    <CRow>
      <CCard className="mb-4">
        <CCardHeader>Available Products</CCardHeader>
        <CCardBody>
          <CInputGroup className="mb-3">
            <CInputGroupText>Product</CInputGroupText>
            <CFormSelect aria-label="Default select example" onChange={(evnt) => { setProduct(products[evnt.target.value].name); console.log(products[evnt.target.value].price) }}>
              {
                products.map((value, index) => {
                  return (
                    <option value={index}>{value.name}</option>
                  )
                })
              }

            </CFormSelect>
          </CInputGroup>

          <CInputGroup className="mb-3">
            <CInputGroupText>Quantity</CInputGroupText>
            <CFormInput aria-label="quantity" type='number' onChange={(evnt) => setQauntity(evnt.target.value)} />
            {/* <CInputGroupText>.00</CInputGroupText> */}
          </CInputGroup>
          <CButton component="a" color="success" href="#" role="button" onClick={(evnt) => addOrder()}>
            Add
          </CButton>
        </CCardBody>
      </CCard>

      <CRow>
        <CCol xs>
          <CCard className="mb-4">
            <CCardHeader>Available Products</CCardHeader>
            <CCardBody>
              <br />
              <CTable align="middle" className="mb-0 border" hover responsive>
                <CTableHead color="light">
                  <CTableRow>
                    <CTableHeaderCell>Name</CTableHeaderCell>
                    <CTableHeaderCell className="text-center">Quantity</CTableHeaderCell>
                    <CTableHeaderCell className="text-center">Price</CTableHeaderCell>
                  </CTableRow>
                </CTableHead>
                <CTableBody>
                  {data.map((item, index) => (
                    <CTableRow v-for="item in tableItems" key={index}>
                      <CTableDataCell>
                        <div>{item.name}</div>
                      </CTableDataCell>
                      <CTableDataCell className="text-center">
                        <div>{item.quantity}</div>
                      </CTableDataCell>
                      <CTableDataCell className="text-center">
                        <div>{item.price}</div>
                      </CTableDataCell>
                    </CTableRow>
                  ))}
                </CTableBody>
              </CTable>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>

      <CButton component="a" color="success" href="#" role="button" onClick={(evnt) => saveOrder()}>
        Check Out
      </CButton>
    </CRow>
  )
}

export default Order
